.App {
  text-align: center;
}

table, tr, th, td {
  border: 1px solid black;
}

table.done {
  background: #225502;
}

.name {
  a:link, a:visited {
    background-color: inherit;
    color: inherit;
    /*text-decoration: none;*/
  }
}
.rarity-Fine .name {
  color: #62A4DA;
}

.rarity-Masterwork .name {
  color: #1a9306;
}

.rarity-Rare .name {
  color: #fcd00b;
}

.rarity-Exotic .name {
  color: #ffa405;
}

.rarity-Ascended .name {
  color: #fb3e8d;
}

.rarity-Legendary .name {
  color: #4C139D;
}
